export const luhn = (cc: string | number): boolean => {
  const digits = String(cc).match(/\d/g)?.map(d => Number(d)).reverse()
  return digits != null
    ? digits
      .map((d, idx) => {
        d = idx % 2 !== 0 ? d * 2 : d
        return d > 9 ? d % 10 + 1 : d
      })
      .reduce((s, d) => s + d, 0) % 10 === 0
    : false
}
